.login-container {
  gap: 10px;
  display: grid;
  place-items: center;
  border-radius: 40px;
  background-color: #d2d2d2;
  margin: 20px;
  padding: 50px;
}

.login-root {
  background-color:#7d95ac;
  height: 100vh;
  display: grid;
  place-items: center;
}

.input {
  box-sizing: border-box;
  outline: none;
  border: solid 2px gray(20);
  color: #292929;
  width: 100%;
  padding: 8px;
  font-size: 12px;
  border-radius: 10px;
}

.btn {
  width: 100%;
  min-width: 280px;
  color: #565656;
  padding: 10px;
  font-size: 14px;
  font-weight: bold;
  border: gray(20);
  border-radius: 10px;
  cursor: pointer;
}

.form {
  padding: 10px;
  text-align: center;
  font-size: 20px;
}